import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import PopupWrapper from "../MembersTab/staff/Mainbody/FinanceManager/FeesStructure/PopupWrapper";
import { imageShowUrl } from "../services/BaseUrl";
import BorderBottom from "../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import style from "../MembersTab/staff/Mainbody/StaffAdmisionAdmin/Moderator/AdmissionModerator.module.css";
import { useGetReviewModeratorList } from "../Department/Institute/Components/DepartmentApi/depart-api";
import QvipleLoading from "../Loader/QvipleLoading";
import styleDyn from "../Department/Institute/AssignStaff/AssignStaffModal.module.css";
const SelectReviewMode = ({
  onClose,
  onSelect,
  instituteId,
  selectedId,
  onDone,
}) => {
  const { t } = useTranslation();
  const [showingDataLoading, setShowingDataLoading] = useState(true);

  const { getReviewModeratorList, getReviewModeratorListRefetch } =
    useGetReviewModeratorList({
      data: {
        id: instituteId,
        role: "LEAVE_REVIEW_ACCESS",
      },
      skip: !instituteId,
    });

  useEffect(() => {
    if (instituteId) {
      setShowingDataLoading(true);
      getReviewModeratorListRefetch();
    }
  }, [instituteId, getReviewModeratorListRefetch]);
  useEffect(() => {
    if (getReviewModeratorList?.all_mods?.length >= 0) {
      setShowingDataLoading(false);
    }
  }, [getReviewModeratorList?.all_mods]);
  return (
    <PopupWrapper onClose={onClose}>
      <div className={style.assign_role_container}>
        <div
          className={styleDyn.assign_title}
          style={{
            width: "100%",
            padding: "1rem",
          }}
        >
          <h5
            style={{
              fontSize: "14px",
              fontWeight: "500",
            }}
          >
            {t("select_review_authority")}
          </h5>
          <div className={styleDyn.is_mulitselect_container} onClick={onDone}>
            <h6
              style={{
                cursor: "pointer",
                fontWeight: "500",
              }}
            >
              {t("select")}
            </h6>
            <img
              src="/images/close-post-icon.svg"
              onClick={onClose}
              alt="staff assign pop close icon"
            />
          </div>
        </div>

        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />
        {getReviewModeratorList?.all_mods?.map((review) => (
          <section
            className={style.selected_staff_container}
            style={{
              cursor: "pointer",
            }}
            onClick={() => onSelect(review)}
            key={review?._id}
          >
            {selectedId?.includes(review?._id) ? (
              <img
                src="/images/checkbox-icon.svg"
                alt="icon"
                style={{
                  height: "1.3rem",
                  borderRadius: "0",
                  cursor: "pointer",
                }}
              />
            ) : (
              <img
                style={{
                  height: "1.3rem",
                  borderRadius: "0",
                  cursor: "pointer",
                }}
                src="/images/box-icon.svg"
                alt="icon"
              />
            )}
            <img
              src={
                review?.access_staff?.staffProfilePhoto
                  ? `${imageShowUrl}/${review?.access_staff?.staffProfilePhoto}`
                  : "/images/member_tab/class/default_avatar.svg"
              }
              alt="staff profile avatar"
            />
            <div className={style.selected_staff_text}>
              <h6>
                {`${review?.access_staff?.staffFirstName} ${
                  review?.access_staff?.staffMiddleName ?? ""
                } ${review?.access_staff?.staffLastName}`}
              </h6>
              <p>Leave Review Authority</p>
            </div>
          </section>
        ))}
        {showingDataLoading && <QvipleLoading />}
      </div>
    </PopupWrapper>
  );
};

export default SelectReviewMode;
